<template>
	<div class="hammerJS">
		<div class="title">这里是hammerJS</div>
		<button class="btn" @click="clean">clean</button>
		<div class="square" ref="square">点这儿</div>
	</div>
</template>
<script>
	import Hammer from 'hammerjs'

  export default {
  	mounted(){
  		this.createHammer();
  	},
		methods:{
			createHammer(){
				var _this = this;
				var myElement = this.$refs.square;
				// We create a manager object, which is the same as Hammer(), but without the presetted recognizers. 
				const mc = new Hammer.Manager(myElement);
				const pinch = new Hammer.Pinch()

     		mc.add([pinch])
     		mc.add(new Hammer.Pan())

				// 点击3次触发tripleTapPP事件（点2次或者点4次都不会触发）
				mc.add( new Hammer.Tap({ event: 'tripleTapPP', taps: 3 }) );
				// 默认点击一次触发的事件
				mc.add( new Hammer.Tap({ event: 'singletap' }) );

				// we want to recognize this simulatenous, so a quadrupletap will be detected even while a tap has been recognized.
				mc.get('tripleTapPP').recognizeWith('singletap');
				// 如果tripleTapPP没有监测到，则响应singletap事件。（加了这句，就不会同时触发两个事件了。注释掉试一下就知道）
				mc.get('singletap').requireFailure('tripleTapPP');

				mc.on("singletap tripleTapPP panstart panmove panend", function(ev) {
				    myElement.textContent += ev.type +" ";
				    _this.scrollToBottom(myElement);	//自动滚动到底
				});
			},
			clean(){
				this.$refs.square.textContent = "";
			},
			scrollToBottom(myElement){

				var s_scrollTop = myElement.scrollTop;
				var s_scrollHeight = myElement.scrollHeight;
				var s_clientHeight = myElement.clientHeight;

				if(s_scrollTop + s_clientHeight >= s_scrollTop){
					//滚动到底
					myElement.scrollTop = s_scrollTop + s_clientHeight;
				}
				
			}
		}
  };
</script>
<style lang="scss">
	.hammerJS{
		position: relative;
		height: 500px;
		widows: 98%;
		border: 1px solid;
		.btn{
			display: block;
			position: relative;
			top: 25%;
			margin: 0 auto;
		}
		.square{
			position: relative;
			top: 30%;
			margin: 0 auto;
			width: 200px;
			height: 200px;
			color: #4490a6;
			background: #d5d5d5;
			overflow: auto;
		}
	}
</style>


