<template>
  <div id="me">
    <div class="btn">
        <button @click='alertToast'>Toast</button>
    </div>
  </div>
</template>
 
<script>
    import Vue from 'vue'
    // import { mapGetters,mapMutations,mapActions  } from 'vuex'
    // import Toast from '../libs/Toast'
    // Vue.use(Toast,{    //支持全局配置
    //     defaultType: "bottom",
    //     duration: "3000"
    // })
    export default {
        name: 'Me',
        methods: {
            alertToast(){
                // console.log(this);
                this.$toast("我是一个Toastaaaaaa");
                // console.log();
                //console.log(this.$toast.bottom("我是一个Toast"));
            }
        }
    }
</script>
 
<style scoped>
    #me {
    width: 80%;
    height: 100px;
    margin: 0 auto;
    border: 1px solid red;
    background: white;
 }
  
 h1 {
    color: red;
    text-align: center;
 }
 .btn{
    display: flex;
    justify-content: space-around;
    align-items: center;
 }
 button{
    border: 1px solid red;
    width: 100px;
    height: 30px;
    background: blue;
    color: white;
    cursor: pointer;
 }
</style>



