import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/home'
import homeChild from '../components/children/homeChild'
import page01 from '../components/page01'
import page02 from '../components/page02'
import page03 from '../components/Toast'
import page04 from '../components/Flex'
import page05 from '../components/hammerJS'
import childPage01 from '../components/children/childPage01'
import childPage02 from '../components/children/childPage02'

Vue.use(Router)

export default new Router({
	mode: 'history',	// 添加 mode: 'history' 之后将使用 HTML5 history 模式，该模式下没有 # 前缀，而且可以使用 pushState 和 replaceState 来管理记录。
	linkActiveClass: 'v-active',
	routes : [
		{
			path: '/',
			component: Home,
			// children:[
			// 	{
			// 		path: 'homeChild',
			// 		name: 'homeChild',
			// 		component: homeChild
			// 	}
			// ]
		},
		{
			path: '/home',
			name: 'home',
			component: Home,
			children:[
				{
					path: 'homeChild',
					name: 'homeChild',
					component: homeChild
				}
			]
		},
		{
			path: '/page01',
			name: 'page01',
			component: page01,
			children:[
				{
					path: 'childPage01',
					name: 'childPage01',
					component: childPage01
				},
				{
					path: 'childPage02',
					name: 'childPage02',
					component: childPage02
				}
			]
		},
		{
			path: '/page02',
			name: 'page02',
			component: page02
		},
		{
			path: '/page03',
			name: 'Toast',
			component: page03
		},
		{
			path: '/page04',
			name: 'flex',
			component: page04
		},
		{
			path: '/page05',
			name: 'hammerJS',
			component: page05
		},
	]
})



