<template>
	<div>
		<!-- <router-view></router-view> -->
		<div class="home">{{homeMsg}}</div>
		<button @click="clicked();$goRoute22(links.url_01)" >{{subMsg01}}</button>
		<!-- <ul class="text-center spacing" style="padding: 0">
      <li v-for="item in links"><a @click="$goRoute22(item.rou)">{{item.text}}</a></li>
    </ul> -->
		<router-view></router-view>
	</div>
</template>

<script>
	import Vue from 'vue'
	export default{
		name: 'home',
		data(){
			return {
				homeMsg: '这里是home',
				subMsg01: '点击看homeChild',
				count_i: 0,
				links:{
					url_01: '/home/homeChild'	//这里的路径层级对应route.js里的path路径，跟自己在windows创建的文件夹名字无关！！！
				}
				// links: [
    //     {
    //       text: '点击看homeChild',
    //       rou: '/homeChild'
    //     },
      // ]
			}
		},
		methods: {
			clicked: function(){
				this.conso();
				this.conso2();
			},
			conso: function() {
				console.log("conso.");
			},
			conso2: function() {
				this.subMsg01 = '再点+'+ this.count_i++;
			},
		}
	}
</script>

<style>
  .home {
    margin-top: 10%;
    font-size: 2.4em;
    font-weight: bold;
  }
  button{
  	padding: 5px;
  	margin: 10px;
  }
</style>


