// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
/*这里可以用两种写法：
	若写成import router from './router'，router文件夹里面的路由文件需要命名成index.js（可能就是默认值）。
	若写成import router from './router/route'，路由文件名可以自定义，如route.js
*/
// import router from './router'
import router from './router/route'
//载入全局css文件
import './assets/css/style.css'
//在main.js载入，全局使用Toast
import Toast from './libs/Toast'

Vue.use(Toast,{    //支持全局配置
    defaultType: "bottom",
    duration: "3000"
})

//路由转跳（自定义方法）
Vue.prototype.$goRoute22 = function(index){
	this.$router.push(index)
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  router,
  render: h => h(App)
})



