<template>
  <div id="app2">
    <img src="./assets/logo.png">
    <!-- <helloWd :myProp1="dataToChildComponents1" :myProp2="dataToChildComponents2"></helloWd> -->
    <Hello-World :myProp1="dataToChildComponents1" :myProp2="dataToChildComponents2" @transferUser="getUser"></Hello-World>
    <div>从子组件(HelloWorld)传递到父组件(App)的数据：</div>
    <div class="user">{{user}}</div>
    <div>
      <ul>
        <!-- $goRoute22方法是自己定义在main.js中的 -->
        <li v-for="item in links" class="nav"><a @click="$goRoute22(item.route)">{{item.text}}</a></li>
      </ul>
      <!-- 路由的页面将渲染在这里儿 -->
      <router-view></router-view>
    </div>
  </div>
  
</template>

<script>
import HelloWorld from './components/HelloWorld';
import util from './libs/util.js';
import Toast from './libs/Toast';

export default {
  name: 'app',
  components: {
    HelloWorld
  },
  data(){
    return {
      dataToChildComponents1: '我是传递到子组件的数据',
      dataToChildComponents2: '我也是传递到子组件的数据',
      user: "",
      links:[
        {
          text: 'home',
          route: '/home'
        },
        {
          text: 'page01',
          route: '/page01'
        },
        {
          text: 'page02',
          route: '/page02'
        },
        {
          text: 'Toast',
          route: '/page03'
        },
        {
          text: 'Flex',
          route: '/page04'
        },
        {
          text: 'hammerJS',
          route: '/page05'
        }
      ]
    }
  },
  created: function(){
    /*方法一：需要在初始化时将this（vm实例）保存，否则无法在window.onscroll = function(){}中调用vm实例 */
    var vmInstance = this;  
    window.onscroll = function(){
      var s = util.ScollPostion();
      // console.log(s);
      if(s.isBottom){
        vmInstance.$toast("到底了!");
      }
      if(s.isTop){
        vmInstance.$toast("到顶了!");
      }
    }
    /*方法二：采用箭头函数，this将一直绑定vm实例*/
    // window.onscroll = ()=>{ 
    //   var s = util.ScollPostion();
    //   if(s.isBottom){
    //     this.$toast("到底了！");
    //   }
    // }
  },
  methods:{
    getUser(msg){
      this.user = msg;
    }
  }
}
</script>

<style scope>
#app2 {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.nav{
  display: inline-block;
  margin: 20px;
  color: #0029ff;
  text-decoration: underline;
}
.nav:hover{
  cursor: pointer;
  color: #6400ff;
}
a.v-active{
  color: red;
}
.user{
  color: red;
}
</style>



