<template>
	<h3 @click="funA">{{msg}}</h3>
</template>

<script type="text/javascript">
	import {gogo} from '../../libs/test1.js';

	export default{
		name: 'page01-A',
		data(){
			return{
				msg: '这里是page01的小弟A',
			}
		},
		methods: {
			funA(){
				gogo(1,2,3);
			}
		}
	}
</script>

<style scoped>
	h3{
		color: red;
	}
</style>


