<template>
	<div>
		<!-- <router-view></router-view> -->
		<h3>page02的Title</h3>
		<div class="pages">{{page02Msg}}</div>
		<div>
			<input  :class="aClass" type="phoneNumber" oninput="if(value.length>11)value=value.slice(0,11)" v-model="phoneNumber">
		</div>
		
	</div>
</template>

<script>
	export default{
		name: 'page02',
		data(){
			return {
				page02Msg: '这里是page02',
				aClass: "b1 b2",
				phoneNumber: "1383231231",
			}
		},
		computed: {
			rightPhoneNumber: function(){
        return /^1(3[0-9]|4[4-9]|5[0-9]|66|7[0-9]|8[0-9]|9[89])[0-9]{8}$/.test(this.phoneNumber)

			}
		},
		watch: {
			phoneNumber: function(){
				if(this.phoneNumber.length == 11 && this.rightPhoneNumber){
            this.aClass = "b1 b3";
        }else{
            this.aClass = "b1 b2";

        }
			}
		},
		methods:{
			getSomething:function () {
			    return "something";
			},
			testAsync: async function() {
			    return Promise.resolve("hello async");
			},
			test: async function () {
			    const v1 = await this.getSomething();
			    const v2 = await this.testAsync();
			    console.log(v1);
			    console.log(v2);
			}

		},
		mounted: function(){
			this.test();
		}
	}
</script>

<style>
	.b3{
		color: blue;
		text-shadow: 1px 1px 20px #45eedf;
	}
</style>


