<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <div class="prop">
      {{myProp1}}
      <br>
      {{myProp2}}
    </div>
    <span>传递数据到父组件：</span>
    <input type="text" v-model="username" @change="setUser">
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      username: ""
    }
  },
  props:['myProp1','myProp2'],
  methods:{
    setUser: function(){
      this.$emit('transferUser', this.username)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.prop{
  color: #ff9632;
}
</style>



